.popup__input-error {
  font-size: 12px;
  line-height: 15px;
  font-weight: 400;
  color: #ff0000;
  width: calc(100% - 2* 36px);
  margin-top: 5px;
  min-height: 15px;
  opacity: 0;
}

@media screen and (max-width: 430px) {
  .popup__input-error {
    width: calc(100% - 2 * 22px);
  }
}